body {
  background: #222;
}
header {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 100px;
  z-index: 100;
}
header .logo img {
  max-width: 150px;
}
.navigation {
  display: flex;
}
.navigation li {
  list-style: none;
  margin: 0 10px;
}
.navigation li a {
  color: #fff;
  text-decoration: none;
  font-weight: 500;
}
.navigation li:hover a {
  color: #de0611;
  transition: all 0.2s ease-in-out;
}
.search {
  position: relative;
  width: 300px;
  height: 40px;
}
.search input {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: #fff;
  background: transparent;
  border: 1px solid rgba(255, 255, 255, 0.5);
  outline: none;
  border-radius: 4px;
  padding: 0 10px 0 45px;
  backdrop-filter: blur(10px);
}
.search input::placeholder {
  color: #fff;
}
.search .fa-search {
  position: absolute;
  top: 50%;
  left: 10px;
  transform: translateY(-50%);
  padding-right: 10px;
  color: #fff;
  border-right: 1px solid #fff;
}
.fa-search {
  height: 20px;
}
.banner {
  position: relative;
  width: 100%;
  height: 800px;
  min-height: 100vh;
  padding: 0 100px;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}
.banner .bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.content {
  position: relative;
  z-index: 1;
  max-width: 550px;
}
.movieTitle {
  max-width: 300px;
}
.banner .content {
  color: rgba(255, 255, 255, 0.5);
  font-weight: 500;
}
.banner .content h4 span {
  padding: 0 10px;
  border-right: 1px solid rgba(255, 255, 255, 0.5);
  font-size: 1.2em;
  font-weight: 500;
}
.banner .content h4 span:first-child {
  padding-left: 0;
}
.banner .content h4 span:last-child {
  border-right: none;
}
.banner .content h4 span i {
  background: #de0611;
  padding: 0 8px;
  display: inline-block;
  font-style: normal;
  color: #fff;
  border-radius: 2px;
}
.banner .content p {
  font-size: 1em;
  font-weight: 300;
  line-height: 1.5em;
  color: #fff;
  padding: 10px 0 20px;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
}
.banner .content .buttons {
  position: relative;
}
.banner .content .buttons a {
  position: relative;
  display: inline-block;
  margin-right: 10px;
  background: #de0611;
  color: #fff;
  padding: 6px 20px 8px;
  text-decoration: none;
  font-weight: 500;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  vertical-align: middle;
  margin-top: 30px;
}
.banner .content .buttons a strong {
  vertical-align: top;
  padding-left: 10px;
}
.banner .content .buttons a img {
  width: 20px;
  height: 20px;
  vertical-align: middle;
}
.banner .content .buttons a:first-child img {
  width: 30px;
  height: 20px;
}
.banner .content .buttons a:nth-child(2) {
  background: rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.banner .content .buttons a:hover:nth-child(2) {
  background: #de0611;
}
.play {
  position: absolute;
  bottom: 50px;
  left: 100px;
  display: inline-flex;
  justify-content: flex-start;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  text-decoration: none;
  letter-spacing: 1px;
  font-size: 1.2em;
  cursor: pointer;
  align-items: center;
  padding: 5px 10px;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.1);
}
.play:hover {
  transition: all 0.2s ease-in-out;
  background-color: #de0611;
}
.play img {
  max-width: 50px;
  margin-right: 10px;
}
.trailer {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  backdrop-filter: blur(20px);
  visibility: hidden;
  opacity: 0;
}
.trailer video {
  max-width: 900px;
  outline: none;
}
.close {
  position: absolute;
  top: 30px;
  right: 30px;
  cursor: pointer;
  filter: invert(1);
  max-width: 32px;
}
.trailer.active {
  visibility: visible;
  opacity: 1;
}
@media (max-width: 1080px) {
  header {
    padding: 20px 50px;
  }
  .banner {
    padding: 100px 50px;
  }
  .banner .play {
    left: 50px;
    font-size: 1em;
  }
  .play img {
    margin-right: 10px;
    min-width: 40px;
  }
}
@media (max-width: 991px) {
  header {
    flex-direction: column;
  }
  .navigation {
    display: flex;
    margin: 10px 0;
    flex-wrap: wrap;
    justify-content: center;
  }
  .trailer video {
    max-width: 90%;
  }
}
@media (max-width: 479px) {
  header {
    padding: 20px 20px;
  }
  .banner {
    padding: 60px 20px;
  }
  .banner .play {
    left: 20px;
    font-size: 0.8em;
  }
  .banner .content h4 span {
    font-size: 1em;
    padding: 0 6px;
  }
  .movieTitle {
    max-width: 280px;
  }
  .banner .content .buttons a:first-child strong {
    padding-left: 25px;
  }
  .search {
    width: 95%;
    height: 40px;
  }
}
